                        </v-list-item-content >
                    </v-list-item>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
            <v-col >
                <v-card>
                    <h3>국내 확진자수 증가추이</h3>
                    <v-switch label="test"></v-switch>
                    <line-chart 
                    v-if="loaded"
                    :chart-data="datacollection">
                    </line-chart>
                </v-card>
            </v-col>
            <v-col>
                <v-card>
                    <h3>국내 확진자수 증가추이</h3>
                    <v-switch label="test"></v-switch>
